import React from 'react';

const Types = ({ types }) => {
  const formatTypes = (typesArr) =>
    [
      ...typesArr.reduce(
        (prevType, currentType) => prevType + '/' + currentType,
        ''
      ),
    ]
      .slice(1)
      .join('');
  return types ? <p>{formatTypes(types)}</p> : <p>No types</p>;
};

export default Types;
