const pokemonDTO = ({ id, name, types: t, sprites, abilities: a }) => {
  const types = t.map((t) => t.type.name);
  const sprite = sprites.front_default;
  const abilities = a.map((a) => a.ability.name);
  return {
    id,
    name,
    abilities,
    types,
    sprite,
  };
};

export default pokemonDTO;
